import React, { useRef, useState } from 'react';
import { FFmpeg } from '@ffmpeg/ffmpeg';
import { fetchFile, toBlobURL } from "@ffmpeg/util"
import NoSsr from '@/compoments/noSsr';

export default function VideoCompressor() {
  const ffmpegRef = useRef(new FFmpeg())
  // const [ready, setReady] = useState(false);
  // const [video, setVideo] = useState(null);
  // const videoRef = useRef(null);
  // const messageRef = useRef(null);
  const [output, setOutput] = useState("");

  const load = async () => {
    const baseURL = "https://unpkg.com/@ffmpeg/core@0.12.10/dist/umd";
    const ffmpeg = ffmpegRef.current;
    await ffmpeg.load({
      coreURL: await toBlobURL(`${baseURL}/ffmpeg-core.js`, "text/javascript"),
      wasmURL: await toBlobURL(
        `${baseURL}/ffmpeg-core.wasm`,
        "application/wasm"
      ),
    });
  }

  const changeVideo: React.ChangeEventHandler<HTMLInputElement> = async (e) => {
    await load();
    const file = e?.target?.files?.[0];
    const ffmpeg = ffmpegRef.current;
    if (!file) return;
    // TODO finish this
    // https://ffmpegwasm.netlify.app/docs/getting-started/usage
    ffmpeg.writeFile("input.mp4", await fetchFile(file))
    await ffmpeg.exec(['-i', 'input.mp4', '-vcodec', 'libx264', '-crf', '28', 'output.mp4']);
    const data = (await ffmpeg.readFile("readFile", 'output.mp4')) as any;
    const videoURL = URL.createObjectURL(new Blob([data.buffer], { type: 'video/mp4' }));
    setOutput(videoURL);
  }

  return <NoSsr>
    <div>
      <input type="file" accept="video/mp4" onChange={changeVideo} />
      <video controls src={output} width="480" />
      <a href={output} download="compressed.mp4">Baixar v√≠deo</a>
    </div>
  </NoSsr>
}
